import React, { memo } from 'react';
import { Handle } from 'reactflow';



export default memo(({ data, isConnectable }) => {
  return (
    <>
      <div>{data.label}</div>

      <Handle
        type="source"
        position="left"
        id="a"
        style={{ top: "30%", }}
        onConnect={(params) => console.log('handle onConnect', params)}
        isConnectable={isConnectable}
        >
      </Handle>
      <Handle
        type="target"
        position="left"
        id="b"
        style={{ top: "70%", }}
        onConnect={(params) => console.log('handle onConnect', params)}
        isConnectable={isConnectable}
      >
      </Handle>
      
      <Handle
        type="target"
        position="right"
        id="c"
        style={{ top: "30%",  }}
        onConnect={(params) => console.log('handle onConnect', params)}
        isConnectable={isConnectable}
        >
      </Handle>
      <Handle
        type="source"
        position="right"
        id="d"
        style={{ top: "70%", }}
        onConnect={(params) => console.log('handle onConnect', params)}
        isConnectable={isConnectable}
        >
      </Handle>

      <Handle
        type="source"
        position="top"
        id="e"
        style={{ left: "45%", }}
        onConnect={(params) => console.log('handle onConnect', params)}
        isConnectable={isConnectable}
        >
      </Handle>
      <Handle
        type="target"
        position="top"
        id="f"
        style={{ left: "55%",  }}
        onConnect={(params) => console.log('handle onConnect', params)}
        isConnectable={isConnectable}
        >
      </Handle>

      <Handle
        type="source"
        position="bottom"
        id="g"
        style={{ left: "55%",  }}
        onConnect={(params) => console.log('handle onConnect', params)}
        isConnectable={isConnectable}
        >
      </Handle>
      <Handle
        type="target"
        position="bottom"
        id="h"
        style={{ left: "45%",  }}
        onConnect={(params) => console.log('handle onConnect', params)}
        isConnectable={isConnectable}
        >
      </Handle>
    </>
  );
});
