import React, { useState, useRef, useCallback } from 'react';
import ReactFlow, {
  ReactFlowProvider,
  addEdge,
  useNodesState,
  useEdgesState,
  updateEdge,
  Controls,
  MiniMap,
  Background,
  MarkerType,
  ConnectionMode,
} from 'reactflow';



import 'reactflow/dist/style.css';
import './index.css';
import Sidebar from './Sidebar';
import CustomNodeEscalator from './customnodes/vt_escalator';
import CustomNodePlatform from './customnodes/platform';
//import CustomEdge from './CustomEdge';
//import ColorSelectorNode from './ColorSelectorNode';
//import SimpleFloatingEdge from './FloatingEdge';
//import CustomNodeBasic from './CustomNodeBasic';

const proOptions = { hideAttribution: true };

const nodeTypes = {
  "node_escalator" : CustomNodeEscalator,
  "node_platform" : CustomNodePlatform,
  //'node_custom_colorpicker': ColorSelectorNode,
  //'customBasic': CustomNodeBasic,
};

const edgeTypes = {
  //'custom': CustomEdge,
  //'customFloating': SimpleFloatingEdge,
};






const initialNodes = [
  {
    id: '1',
    type: 'node_escalator', // type: 'input',
    data: { label: 'Escalator 1A',},
    position: { x: 350, y: 450 },
  },
  {
    id: '4',
    type: 'node_platform', // type: 'input',
    data: { label: 'Platform: 4B' },
    position: { x: 525, y: 600 },
  },
];


const initialEdges = [
  {
    id: '2-3',
    type: 'smoothstep',
    source: '1',
    target: '4',
    sourceHandle: 'd',
    targetHandle: 'b',
    animated: true,
    label: 'pedestrian flow',
    markerEnd: { type: MarkerType.Arrow },
  },
];


// fitView defines the initial rendering 
const fitViewOptions = {
  padding: 1,
  //includeHiddenNodes: boolean,
  //minZoom: number,
  //maxZoom: number,
  //duration: number,
};

let id = 0;
const getId = () => `appnode_${id++}`;

const StationSizingApp = () => {
  const reactFlowWrapper = useRef(null);
  const [nodes, setNodes, onNodesChange] = useNodesState(initialNodes);
  const [edges, setEdges, onEdgesChange] = useEdgesState(initialEdges);
  const [reactFlowInstance, setReactFlowInstance] = useState([null]);

  const onConnect = useCallback(
    (params) =>
      console.log(params) ||
      setEdges((eds) =>
        //addEdge(params, eds)
        addEdge({ ...params, type: 'smoothstep', animated: true, markerEnd: { type: MarkerType.Arrow } }, eds)
      ),
    []
  );


  const onDragOver = useCallback((event) => {
    event.preventDefault();
    event.dataTransfer.dropEffect = 'move';
  }, []);

  const onDrop = useCallback(
    (event) => {
      event.preventDefault();

      const reactFlowBounds = reactFlowWrapper.current.getBoundingClientRect();
      const type = event.dataTransfer.getData('application/reactflow');

      // check if the dropped element is valid
      if (typeof type === 'undefined' || !type) {
        return;
      }

      const position = reactFlowInstance.project({
        x: event.clientX - reactFlowBounds.left,
        y: event.clientY - reactFlowBounds.top,
      });
      const newNode = {
        id: getId(),
        type,
        position,
        data: { label: `${type} node` },
      };

      setNodes((nds) => nds.concat(newNode));
    },
    [reactFlowInstance]
  );

  const onEdgeUpdateStart = useCallback(() => {
    reactFlowWrapper.current = false;
  }, []);

  const onEdgeUpdate = useCallback((oldEdge, newConnection) => {
    reactFlowWrapper.current = true;
    setEdges((els) => updateEdge(oldEdge, newConnection, els));
  }, []);

  const onEdgeUpdateEnd = useCallback((_, edge) => {
    if (!reactFlowWrapper.current) {
      setEdges((eds) => eds.filter((e) => e.id !== edge.id));
    }
    reactFlowWrapper.current = true;
  }, []);







  return (
    <div className="station-sizing-app" /* this classname is applied for each nodes */ > 
        <div>

    </div>
      <ReactFlowProvider>
        <div className="reactflow-wrapper" ref={reactFlowWrapper}>
          <ReactFlow
            nodes={nodes}
            edges={edges}
            onNodesChange={onNodesChange}
            onEdgesChange={onEdgesChange}
            onEdgeUpdate={onEdgeUpdate}
            onEdgeUpdateStart={onEdgeUpdateStart}
            onEdgeUpdateEnd={onEdgeUpdateEnd}
            onConnect={onConnect}
            edgeTypes={edgeTypes}
            nodeTypes={nodeTypes}
            snapToGrid={true}
            onInit={setReactFlowInstance}
            onDrop={onDrop}
            onDragOver={onDragOver}
            fitView
            fitViewOptions={fitViewOptions}
            attributionPosition="top-left"          
            connectionMode={ConnectionMode.Loose}
            proOptions={proOptions}
          >
            <MiniMap />
            <Controls />
            <Background />
          </ReactFlow>
        </div>
        <Sidebar />
      </ReactFlowProvider>
    </div>
  );
};

export default StationSizingApp;
